<!--数据统计-->
<template>
  <div class="static">
    <div class="title">数据统计</div>

    <div class="container">

      <div class="header">
        <div></div>
        <div class="nav">
          <ul>
            <li :class="{active:searchObj.type==='teacher'}" @click="handleSelectType('teacher')">教师</li>
            <li :class="{active:searchObj.type==='school'}" @click="handleSelectType('school')">学校</li>
            <li :class="{active:searchObj.type==='area'}" @click="handleSelectType('area')">区域</li>
          </ul>
        </div>
        <div v-show="false" class="btn-list">
          <div class="display"> <i class="iconfont icon-real"></i> 大数据展示</div>
          <div class="export"> <i class="iconfont icon-real"></i> 导出</div>
        </div>
      </div>

      <div class="main">
          <div class="main-header">
            <el-input 
            v-if="searchObj.type==='teacher'" 
            size='mini' 
            style="width:200px;margin-right:18px" 
            v-model="searchObj.teacher" 
            placeholder="请输入教师姓名"></el-input>
            <div v-if="searchObj.type!='area'" class="school">
              <span>学校：</span>
              <el-input
              placeholder='请选择学校'
              @click.native.stop="showSchoolTree = true"
              v-model="searchObj.school.label"
              suffix-icon="el-icon-search" 
              size='mini'></el-input>
              <div class="tree-wrap" v-if="showSchoolTree" v-clickOutSide="handleHideSelectTree">
                <el-scrollbar style="height:100%">
                  <el-tree
                    class="filter-tree"
                    :data="treeData"
                    :props="defaultProps"
                    @node-click="handleNodeClick"
                    :filter-node-method="filterNode"
                    default-expand-all
                    ref="tree">
                  </el-tree>  
                </el-scrollbar>
              </div>
            </div>
            <div v-if="searchObj.type==='area'" class="area">
              区域级别：
              <el-select size='mini' v-model="searchObj.area">
                <el-option 
                v-for="item in areaList" 
                :key="item.code"
                :label="item.text" 
                :value="item.code">
                </el-option>
              </el-select>
            </div>
            <div class="start-time" style="margin-left:18px">
              开始日期：
              <el-date-picker
                v-model="searchObj.start"
                type="date"
                size='mini'
                placeholder="选择日期">
              </el-date-picker>
            </div>
            <div class="end-time" style="margin-left:18px">
              结束日期：
              <el-date-picker
                v-model="searchObj.end"
                type="date"
                size='mini'
                placeholder="选择日期">
              </el-date-picker>
            </div>
            <el-button @click="handleSearch" style="margin-left:18px" size='mini'>查询</el-button>
            <span @click="handleClearSearch" style="margin-left:18px;cursor:pointer">清空</span>
          </div>
          <div class="main-table">
            <template>
              <el-table
                :data="tableData"
                style="width: 100%">
                <el-table-column
                  prop="order"
                  label="排名"
                  >
                </el-table-column>
                <el-table-column
                  v-if="searchObj.type==='teacher'"
                  prop="userName"
                  label="教师"
                  align="center"
                  >
                </el-table-column>
                <el-table-column
                  v-if="searchObj.type==='teacher'"
                  prop="phone"
                  align="center"
                  label="手机号">
                </el-table-column>
                <el-table-column
                  v-if="searchObj.type==='school'"
                  prop="schoolName"
                  align="center"
                  label="学校名称">
                </el-table-column>
                <el-table-column
                  v-if="searchObj.type==='area'"
                  prop="name"
                  align="center"
                  label="区域">
                </el-table-column>
                <el-table-column
                  prop="num"
                  align="center"
                  label="开课数量">
                </el-table-column>
              </el-table>
            </template>
          </div>
          <div class="page-wrap">
            <el-pagination
            v-if="pagination.total / pagination.pageSize > 1"
              @current-change="handleCurrentChange"
              :current-page="pagination.currPage"
              :page-size="pagination.pageSize"
              layout="total, prev, pager, next, jumper"
              :total="pagination.total">
            </el-pagination>
          </div>
      </div>

    </div>
    
  </div>
</template>

<script>
export default {
  components:{
    orgSelect:() => import("@/components/org-select")
  },
  data(){
    return {
      showSchoolTree:false,
      treeData:[],
      defaultProps:{
        children: 'children',
        label: 'displayName',
        value:'id' 
      },
      /* tree */
      areaList:[],
      searchObj:{
        type:'teacher',  // teacher school area
        teacher:'',
        school:{
          value:'',
          label:''
        },
        area:'',
        start:'',
        end:''
      },
      pagination:{
        currPage:1,
        pageSize:10,
        total:0
      },
      tableData: []
    }
  },
  created(){
    this.getTeacher();
    this.getTreeSchool();
    this.getAreaTree();
  },
  computed:{
    filterText(){
      return this.searchObj.school.label
    }
  },
  watch: {
    filterText(val) {
      if(val) {
        this.$refs.tree.filter(val);
      }
    }
  },
  methods:{
    getAreaTree(){
      const params = {
        type:["area_level"]
      }
      this.$api.dictSelect(params).then(res => {
        this.areaList = res.data[0].data;
      })
    },
    /* getTeacher */
    getTeacher(){
      const params = {
        currPage:this.pagination.currPage,
        pageSize:this.pagination.pageSize,
        name:this.searchObj.teacher,
        school:this.searchObj.school.value,
        start:String(this.searchObj.start),
        end:String(this.searchObj.end),
      }
      this.$api.getTeacher(params).then(res => {
        this.tableData = res.data.recordList.map((item,i) => Object.assign(item,{order:i+1}))
        this.pagination.total = res.data.recordCount;
      })
    },
    /* getSchool */
    getSchool(){
      const params = {
        currPage:this.pagination.currPage,
        pageSize:this.pagination.pageSize,
        school:this.searchObj.school.value,
        start:String(this.searchObj.start),
        end:String(this.searchObj.end),
      }
      this.$api.getSchool(params).then(res => {
        this.tableData = res.data.recordList.map((item,i) => Object.assign(item,{order:i+1}))
        this.pagination.total = res.data.recordCount;
      })
    },
    /* getArea */
    getArea(){
      const params = {
        currPage:this.pagination.currPage,
        pageSize:this.pagination.pageSize,
        level:this.searchObj.area,
        start:String(this.searchObj.start),
        end:String(this.searchObj.end),
      }
      this.$api.getArea(params).then(res => {
        if(res.data) {
          this.tableData = res.data.recordList.map((item,i) => Object.assign(item,{order:i+1}))
          this.pagination.total = res.data.recordCount;
        } else {
           this.tableData = [];
           this.pagination.total = 0;
        }
      })
    },
    /* 清空 */
    handleClearSearch(){
      this.pagination.currPage = 1;
      this.searchObj.teacher='';
      this.searchObj.school.label='';
      this.searchObj.school.value='';
      this.searchObj.area='';
      this.searchObj.start='';
      this.searchObj.end='';
      this.handleSelectType(this.searchObj.type)
    },  
    handleSelectType(type) {
      // this.handleClearSearch();
      this.searchObj.type = type;
      if(type === 'teacher'){
        this.getTeacher()
      } else if(type === 'school') {
        this.getSchool()
      } else {
        this.getArea()
      }
    },
    handleCurrentChange(current) {
      this.pagination.currPage = current;
      if(this.searchObj.type === 'teacher') {
        this.getTeacher()
      } else if (this.searchObj.type === 'school') {
        this.getSchool()
      } else {
        this.getArea()
      }
    },
    handleSearch(){
      // console.log(typeof this.searchObj.start)
      this.handleCurrentChange(1)
    },
    getTreeSchool(){
      const params = {
        level:0,
        onlyOrg:1,
        parentId:0,
        status:0
      }
      this.$api.institutions(params).then(res => {
        this.treeData = res.data;
      })
    },
    handleHideSelectTree(){
      this.showSchoolTree = false
    },
    handleNodeClick(data){
      if(!data.next) {
        this.searchObj.school = {
          value:data.id,
          label:data.displayName
        }
        this.handleHideSelectTree()
      }
    },
    filterNode(value, data){
      if (!value) return true;
      return data.displayName.indexOf(value) !== -1;
    }
  }
}
</script>

<style lang="scss" scoped>
.static {padding: 32px;height: 100%}
.static > .title {color: #55595C;font-weight: bold;line-height: 1}
.container {margin-top: 24px;height: calc(100% - 38px);}
.container > .header {display: flex;justify-content: center;align-items: center;}
.header .nav {
  ul li {
    display: inline-block;
    width: 52px;height: 28px;
    text-align: center;line-height: 28px;
    color: #909399;
    margin-right: 37px;
    cursor: pointer;
    &.active {color: #fff;background: #4C84FF;border-radius: 14px;}
  }
}
.header .btn-list div {display: inline-block;color: #4C84FF;
border: 1px solid #4C84FF;height: 28px;line-height: 26px;cursor: pointer;
padding: 0 10px;border-radius: 14px;margin-left: 16px;
i {color: inherit}}

.container .main {
  height: calc(100% - 50px);
  margin-top: 24px;
  padding: 24px 24px 34px;
  border-radius: 6px;
  background: #fff;
  box-shadow: 0px 5px 20px 0px rgba(23,38,71,0.05);
} 
.container .main .main-header {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  .school {
    position: relative;
    display: flex;
    align-items: center;
  }
  .school>span {
    min-width: 46px;
  }
  .school .tree-wrap {
    border-radius: 4px;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    width: 175px;
    z-index: 999;
    position: absolute;
    right: 0;
    top: 30px;
    background: #fff;
    height: 200px;
  }
}
.page-wrap {text-align: right;margin-top: 20px;}
</style>
<style>
.static .el-tree {
  background: #fff;
}
</style>